@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgba(255, 255, 255, 0.4)!important;
	background-blend-mode: lighten;
}

.background {
	background: url("https://cdn.shopify.com/s/files/1/2101/3083/products/5477.jpg?v=1558639341")no-repeat center center fixed;
	background-size: cover;
	min-height: 100vh;
	display: flex;
	background-color: rgba(255, 255, 255, 0.8)!important;
	background-blend-mode: lighten;
	flex-direction: column;
	align-items: center;
}

#nav_user {
	color: white;
}

.top-nav-collapse {
	background-color: #24355C;
}



.white-txt {
	color: white;
	display: inline;
}

.card {
	margin-top: 5px;
	background: #eee;
	padding: 1em;
	min-width: 100%;
/**	overflow: scroll; **/
}

.card-close {
	cursor: pointer;
}

img.preview {
	max-width: 250px;
	max-height: 250px;
}



.table td {
	font-size: .9rem;
	font-weight: 300;
}

.table-container .row {
	min-width: 100%;
}

.table-container .row div {
	font-size: .9rem;
	margin: 0px;
	padding: 1%;
}


/* change navbar background on collapse */

@media (max-width: 768px) {
	.white-txt {
		display: none;
	}
	.collapse {
		background: rgba(0, 0, 0, 0.8);
		padding-left: 15px;
		z-index: 999;
	}
	.card {
		margin-top: 20px;
		background: #eee;
	}
	.table th,
	.table .btn {
		font-size: .5rem;
	}
	.table td {
		font-size: .4rem;
	}
	.table-container .row div {
		font-size: .3rem;
	}
}

.table-container {
	/** overflow: scroll; **/
	margin: 0px;
  padding: 0px;
  border-radius: .2em ;
}

.table-header div {
	text-align: left;
	font-weight: bold;
	background-color: rgb(233, 236, 239);
	border: 1px solid rgb(222, 226, 230);
	color: rgb(94, 100, 107);
}

.table-row div {
	text-align: left;
	border: 1px solid rgb(222, 226, 230);
	color: rgb(94, 100, 107);
}

.recipe-details div {
	border: 0px;
	margin: 0px;
	padding: 0px;
	text-align: center;
	padding-right: 2%;
}

.dashboard-boxes .row{
	padding:1%;
}



.form-wrapper {

  margin: 0 auto;
}
.App {
  text-align: center;
  margin-top: 30px;
}
nav {
  /** width: 300px; **/
  margin: 0 auto;
  background: #282c34;
  height: 70px;
  line-height: 70px;
}
nav .dropdown
{
  line-height: normal;
  border:3px solid red;
  margin:0px;
  display:block;
}
nav ul li {
  display: inline;
  list-style-type: none;
  text-align: center;
  padding: 30px;
}
nav ul li a {
  margin: 50px 0;
  font-weight: bold;
  color: white;
  text-decoration: none;
}
nav ul li a:hover {
  color: white;
  text-decoration: none;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.um-main-body {
  height:100%;
}


.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.6);
	display: inline-block;
  }
  .modal .modal-main {
	position: fixed;

	height: auto;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
  }
  
  .display-block {
	display: block;
  }
  
  .display-none {
	display: none;
  }


  .date-picker-input, .google-locations-input{
    border: none;
    padding:0px;

  }

  .date-picker-input::-webkit-input-placeholder, .google-locations-input::-webkit-input-placeholder  {
    color: black;
  }
  .session-card-title {
  
  }
  .session-card-title:hover,
  .board-card-title:hover {
    color: blue;
    cursor: pointer;
  }

  .board-select{
    padding-left : 0px;
    margin-left : 0px;
  }

  .detail-line{
    padding-bottom:1em;
  }

  .text-area {
    min-height:150px;
  }
.Home {
	text-align: center;
}

.home-background {
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: rgba(255, 255, 255, 0.1)!important;
}

.intro {
	padding: 10px;
	background-color: rgba(0, 0, 0, 0.5);
	color: white;
	margin-top: -2em;
}

.Home-logo {
	padding: 10px;
	background-color: rgba(0, 0, 0, 0.5);
	-webkit-animation: Home-logo-spin infinite 20s linear;
	        animation: Home-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
	border-radius: 40px;
}

@-webkit-keyframes Home-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes Home-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.font-size38 {
    font-size: 38px;
}
.team-single-text .section-heading h4,
.section-heading h5 {
    font-size: 36px
}

.team-single-text .section-heading.half {
    margin-bottom: 20px
}

@media screen and (max-width: 1199px) {
    .team-single-text .section-heading h4,
    .section-heading h5 {
        font-size: 32px
    }
    .team-single-text .section-heading.half {
        margin-bottom: 15px
    }
}

@media screen and (max-width: 991px) {
    .team-single-text .section-heading h4,
    .section-heading h5 {
        font-size: 28px
    }
    .team-single-text .section-heading.half {
        margin-bottom: 10px
    }
}

@media screen and (max-width: 767px) {
    .team-single-text .section-heading h4,
    .section-heading h5 {
        font-size: 24px
    }
}


.team-single-icons ul li {
    display: inline-block;
    border: 1px solid #02c2c7;
    border-radius: 50%;
    color: #86bc42;
    margin-right: 8px;
    margin-bottom: 5px;
    transition-duration: .3s
}

.team-single-icons ul li a {
    color: #02c2c7;
    display: block;
    font-size: 14px;
    height: 25px;
    line-height: 26px;
    text-align: center;
    width: 25px
}

.team-single-icons ul li:hover {
    background: #02c2c7;
    border-color: #02c2c7
}

.team-single-icons ul li:hover a {
    color: #fff
}

.team-social-icon li {
    display: inline-block;
    margin-right: 5px
}

.team-social-icon li:last-child {
    margin-right: 0
}

.team-social-icon i {
    width: 30px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    font-size: 15px;
    border-radius: 50px
}

